.task__list {
    padding: 16px 16px;
}
.task {
    background-color: white;
    padding: 16px 16px;
    display: flex;
}
.task:not(:last-child) {
    margin-bottom: 16px;
}
.task__description {
    padding: 0 16px;
}
.checkbox {
    border: 2px solid #c1c1c1;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #32c145;
    font-size: 36px;
    padding-bottom: 6px;
    padding-left: 3px;
}
.animation__pin {
    animation: animation__pin 500ms cubic-bezier(0.18, 0.89, 0.63, 1.33);
}
@keyframes animation__pin {
    from {
        font-size: 60px;
    }
    to {

    }
}